.profile-setup-container {
  max-width: 550px;
  padding-top: 40px;
  position: relative;
  overflow: hidden;
}

.progress-bar {
  background-color: var(--border-color);
  border-radius: 4px;
  width: 90%;
  height: 8px;
  position: absolute;
  top: 15px;
  left: 5%;
}

.progress-bar-inner {
  background-color: var(--primary-color);
  border-radius: 4px;
  width: 0%;
  height: 100%;
  transition: width .5s ease-in-out;
}

.setup-step {
  text-align: center;
  flex-direction: column;
  align-items: center;
  animation: .5s ease-in-out fadeIn;
  display: none;
}

.setup-step.active {
  display: flex;
}

.setup-step h1 {
  margin-bottom: .5rem;
  font-size: 1.8rem;
}

.setup-step p {
  color: var(--light-text-color);
  margin-bottom: 2rem;
}

.setup-step form {
  width: 100%;
}

.gender-selection {
  gap: 8px;
  width: 100%;
  margin: 8px 0;
  display: flex;
}

.gender-selection label {
  border: 2px solid var(--border-color);
  cursor: pointer;
  border-radius: 10px;
  flex: 1;
  justify-content: center;
  align-items: center;
  height: 40px;
  padding: 0;
  transition: all .3s;
  display: flex;
}

.gender-selection input[type="radio"] {
  display: none;
}

.gender-selection span {
  font-weight: 600;
  line-height: 40px;
}

.gender-selection input[type="radio"]:checked + span {
  color: var(--primary-color);
}

.gender-selection label:has(input[type="radio"]:checked) {
  border-color: var(--primary-color);
  background-color: #e8f5e9;
}

.preferences-grid {
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 1rem;
  width: 100%;
  margin-bottom: 2rem;
  display: grid;
}

.preference-card {
  border: 2px solid var(--border-color);
  cursor: pointer;
  background-color: #f9f9f9;
  border-radius: 15px;
  flex-direction: column;
  align-items: center;
  gap: .5rem;
  padding: 1rem;
  transition: all .3s;
  display: flex;
}

.preference-card i {
  color: var(--light-text-color);
  font-size: 2rem;
  transition: all .3s;
}

.preference-card span {
  font-size: .9rem;
  font-weight: 600;
  transition: all .3s;
}

.preference-card:hover {
  border-color: var(--primary-color);
  transform: translateY(-5px);
}

.preference-card.selected {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  color: var(--white-color);
  transform: scale(1.05);
}

.preference-card.selected i, .preference-card.selected span {
  color: var(--white-color);
}

.button-group {
  gap: 1rem;
  width: 100%;
  display: flex;
}

.button-group button {
  flex-grow: 1;
}

.social-input {
  width: 100%;
  margin-bottom: 1rem;
  position: relative;
}

.social-input i {
  color: var(--light-text-color);
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
}

.social-input input {
  padding-left: 40px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
