.profile-setup-container {
    max-width: 550px;
    overflow: hidden; /* Adımlar arası geçiş için */
    position: relative;
    padding-top: 40px; /* İlerleme çubuğu için yer aç */
}

.progress-bar {
    position: absolute;
    top: 15px;
    left: 5%;
    width: 90%;
    height: 8px;
    background-color: var(--border-color);
    border-radius: 4px;
}

.progress-bar-inner {
    width: 0%;
    height: 100%;
    background-color: var(--primary-color);
    border-radius: 4px;
    transition: width 0.5s ease-in-out;
}

.setup-step {
    display: none;
    flex-direction: column;
    align-items: center;
    text-align: center;
    animation: fadeIn 0.5s ease-in-out;
}

.setup-step.active {
    display: flex;
}

.setup-step h1 {
    font-size: 1.8rem;
    margin-bottom: 0.5rem;
}

.setup-step p {
    color: var(--light-text-color);
    margin-bottom: 2rem;
}

.setup-step form {
    width: 100%;
}

/* Cinsiyet Seçimi */
.gender-selection {
    display: flex;
    gap: 8px; /* Textbox'lar ile aynı boşluk */
    width: 100%;
    margin: 8px 0; /* Textbox'lar ile aynı dikey boşluk */
}
.gender-selection label {
    flex: 1; /* Genişliği eşit bölüş */
    display: flex;
    justify-content: center; /* İçeriği YATAY olarak ortala */
    align-items: center;    /* İçeriği DİKEY olarak ortala */
    height: 40px; /* Textbox'lar ile aynı yükseklik */
    padding: 0; /* Padding'i sıfırla, hizalamayı flex'e bırak */
    border: 2px solid var(--border-color);
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
}
.gender-selection input[type="radio"] {
    display: none;
}
.gender-selection span {
    font-weight: 600;
    line-height: 40px; /* Buton yüksekliği ile aynı yaparak dikey ortalamayı garantile */
}
.gender-selection input[type="radio"]:checked + span {
    color: var(--primary-color);
}
.gender-selection label:has(input[type="radio"]:checked) {
    border-color: var(--primary-color);
    background-color: #e8f5e9;
}

/* Adım 2: Tercihler */
.preferences-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 1rem;
    width: 100%;
    margin-bottom: 2rem;
}

.preference-card {
    background-color: #f9f9f9;
    border: 2px solid var(--border-color);
    border-radius: 15px;
    padding: 1rem;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.preference-card i {
    font-size: 2rem;
    color: var(--light-text-color);
    transition: all 0.3s ease;
}

.preference-card span {
    font-weight: 600;
    font-size: 0.9rem;
    transition: all 0.3s ease;
}

.preference-card:hover {
    transform: translateY(-5px);
    border-color: var(--primary-color);
}

.preference-card.selected {
    background-color: var(--primary-color);
    border-color: var(--primary-color);
    color: var(--white-color);
    transform: scale(1.05);
}

.preference-card.selected i,
.preference-card.selected span {
    color: var(--white-color);
}

.button-group {
    display: flex;
    gap: 1rem;
    width: 100%;
}

.button-group button {
    flex-grow: 1;
}

/* Adım 3: Sosyal Medya */
.social-input {
    position: relative;
    width: 100%;
    margin-bottom: 1rem;
}

.social-input i {
    position: absolute;
    left: 15px;
    top: 50%;
    transform: translateY(-50%);
    color: var(--light-text-color);
}

.social-input input {
    padding-left: 40px;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}
